







































module.exports =
  name: 'introText'
  components:
    Typed: require './Typed'

  data: ->
    startServiceId: 2
    lineDone: 0
    currentServicePosition: 0
    currentService: 'Strategy'
    services: ['Interactive Strategy', 'Digital Design', 'Virtual Reality', 'Augmented Reality']

  mounted: ->
    @$watch 'lineDone', (val)=>
      if val is @startServiceId
        setInterval ()=>
          if @currentServicePosition == @services.length - 1
            @currentServicePosition = 0
          else
            @currentServicePosition += 1
        , 4000

  computed:
    service: -> return @services[@currentServicePosition]
    consoleTexts: ->  return @$store.state.consoleTexts
    port: -> return @$store.state.port

  methods:
    openOverlay: (view)->
      if @port != 'mobile'
        overlay = view
        overlay.orientation = 'portrait'
        @$store.dispatch 'openServicesOverlay', view





