



































module.exports =
  name: 'ourContact'
  components:
    Typed: require './Typed'

  methods:
    openOverlay: (view)->
      overlay = view
      overlay.orientation = 'portrait'
      @$store.dispatch 'openServicesOverlay', view

  computed:
    consoleTexts: ->  return @$store.state.consoleTexts




