










module.exports =
  name: 'typed'

  props:
    str:
      type: String
      required: true
    typeSpeed:
      type: Number
      default: 50
    delay:
      type: Number
      default: 700
    cursorChar:
      type: String
      default: '|'
    cleanCursor:
      type: Boolean
      default: false

  data: ->
    typed: []
    strIndex: 0
    sentenceIndex: 0
    shouldShowCursor: true
    bracket: false
    logo: false

  mounted: ->
    # make typeSpeed not less than 16
    @typeSpeed = Math.max(@typeSpeed, 16)
    setTimeout =>
      @type()
    , @delay

    @$watch 'cleanCursor', (newVal, oldVal)=>
      if newVal == true
        @shouldShowCursor = false
      if newVal == false
        @shouldShowCursor = true

  methods:
    type: ()->
      if @strIndex is @str.length # type finish!
        if @cleanCursor then @shouldShowCursor = false
        @$emit('done')
      else
        setTimeout =>
          if @str[@strIndex - 1] is '[' then @bracket = true
          if @str[@strIndex] is ']' then @bracket = false

          if @str[@strIndex] is 'h' and @str[@strIndex + 1] is 'o' and @str[@strIndex + 2] is 't' then @logo = true
          if @str[@strIndex] is ' ' and @str[@strIndex - 1] is 'x' and @str[@strIndex - 2] is 'a' then @logo = false

          if !@bracket and !@logo then typeClass = 'normal'
          if @bracket then typeClass = 'bracket'
          if @logo then typeClass = 'logo'
          @typed.push
            letter: @str[@strIndex]
            typeClass: typeClass
          @strIndex += 1
          @type()
        , @typeSpeed





