


















module.exports =
  name: 'footerPanel'

  computed:
    soundIsOn: ->      return @$store.state.soundIsOn
    consoleTexts: ->   return @$store.state.consoleTexts

  methods:
    toggleSound: ->
      @$store.commit 'TOGGLE_SOUND', !@soundIsOn

    openPortfolioWindow: ()->
      @$router.push('/work')

    goBackToIntro: ->
      @$store.commit 'SET_ENTRY_INDEX', 1

    openOverlay: ->
      @$router.push('/services')




