













module.exports =
  name: 'rubbishBin'

  methods:
    openOverlay: (view)->
      @$store.commit 'SET_OVERLAY_IS_OPEN', true
      @$store.commit 'SET_ACTIVE_OVERLAY', view
  computed:
    overlays: -> return @$store.state.overlays




